 /* SKILLS */
 .image-wrapper {
  position: relative;
}
.image-wrapper img {
  display: block;
  margin-left: 0;
  margin-right: 0;
  padding-left: 10px;
  padding-right: 10px;
  max-width: 100%;
  max-height: 100%;
  object-fit: scale-down;
}
.image-wrapper .hide-text {
  opacity: 0;
  position: relative;
  bottom: 0px;
  left: 0px;
  right: 0px;
  padding: 0px;
  color: #393838;
  text-decoration: none;
  text-align: center;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  -webkit-transition: opacity 500ms;
  -moz-transition: opacity 500ms;
  -o-transition: opacity 500ms;
  transition: opacity 500ms;

}
.image-wrapper:hover .hide-text {
  opacity: 0.8;
}

/* SOCIALS */
.social-wrapper{
